import React, {Component} from 'react';
import {Aside, Section} from "../../components";
import MainSection from "./Main/main-section";
import AboutSection from "./AboutUs/about-section";
import ShowcasesSection from "./Showcases/showcases-section";
import ContactsSection from "./Contacts/contacts-section";
import {Events, scroller} from "react-scroll/modules/index";
import classNames from 'classnames';
import _ from 'lodash';
import Capabilities from "./AboutUs/capabilities";
import {getDimensions} from "../../utils/index";
import './landing.scss'

const LINKS = [
  ".main",
  ".aboutUs",
  ".capabilities",
  ".case-studies",
  ".contacts"
];

const ASIDE_LINKS = [
  ".main",
  ".aboutUs",
  ".case-studies",
  ".contacts"
];

const LINKS_TO_ASIDE_LINKS = {
  0:0,
  1:1,
  2:1,
  3:2,
  4:3,
  5:4
};

export default class Landing extends Component {
  shouldComponentUpdate(nextState, nextProps){
    return !_.isEqual(nextState, this.state) || !_.isEqual(nextProps, this.props)
  }

  constructor(props) {
    super(props);

    this.state = {
      activeSection: 0,
      scrollEnabled: true
    };

    this._handleScroll = _.throttle(({deltaY}) => {
      if(this.state.scrollEnabled && getDimensions().width > 860){
        let scrollTo = null;

        if(deltaY > 0 && this.state.activeSection < LINKS.length - 1){
          scrollTo = LINKS[this.state.activeSection + 1]
        } else if(deltaY < 0 && this.state.activeSection > 0){
          scrollTo = LINKS[this.state.activeSection - 1]
        }

        if(scrollTo){
          scroller.scrollTo(scrollTo, {
            smooth: true,
            duration: 500,
            containerId: "scrollContainer"
          })
        }
      }
    }, 500)
  }

  componentDidMount(){
    Events.scrollEvent.register('begin', (to) => {
      const activeSection = LINKS.indexOf(to);
      this.setState({ scrollEnabled: false, activeSection})
    });

    Events.scrollEvent.register('end', (to) => {
      this.setState({ scrollEnabled: true })
    })
  }

  onScroll(event) {
    this.props.onScroll(event)
  }

  handleWheel(event) {
    event.persist();
    this._handleScroll(event);
  }

  render() {
    return (
      <>
        <Aside links={ASIDE_LINKS} activeIndex={LINKS_TO_ASIDE_LINKS[this.state.activeSection]}/>
        <div className={classNames('application-container', this.state.scrollEnabled ? 'smooth' : 'rough')}
             id="scrollContainer"
             onScroll={this.onScroll.bind(this)}
             onWheel={this.handleWheel.bind(this)}>
          <Section key=".main" link=".main"
                   className={this.state.activeSection === 0 ? "focused" : null}>
            <MainSection/>
          </Section>
          <Section key=".aboutUs" link=".aboutUs"
                   className={this.state.activeSection === 1 ? "focused" : null} >
            <AboutSection/>
          </Section>
          <Section key=".capabilities" link=".capabilities"
                   className={this.state.activeSection === 2 ? "focused" : null}>
            <Capabilities/>
          </Section>
          <Section key=".case-studies" link=".case-studies"
                   className={this.state.activeSection === 3 ? "focused" : null}>
            <ShowcasesSection/>
          </Section>
          <Section key=".contacts" link=".contacts"
                   className={this.state.activeSection === 4 ? "focused" : null}>
            <ContactsSection/>
          </Section>
        </div>
      </>
    )
  }
}
