import React, {Component} from 'react';
import './navbar.scss'
import {NavLink} from 'react-router-dom';
import classnames from 'classnames'

const buildLink = (link, text) => (
  <span>
    <NavLink className={"navigation-link"} to={link} activeClassName={'active-link'} exact={true}>{text}</NavLink>
  </span>
);

export default class Aside extends Component {

  constructor(props) {
    super(props);

    this.state = {
      prevScrollPosition: window.pageYOffset,
      visible: true
    };
  }

  render() {
    return (
      <nav className={classnames("navbar-container", "not-for-print", {
             "navbar--hidden": !this.state.visible
           })}>
        {buildLink('/', 'Home')}
        {buildLink('/case-studies/', 'Case Studies')}
      </nav>
    )
  }

  handleScroll = () => {
    const { prevScrollPosition } = this.state;

    const currentScrollPos = window.pageYOffset;
    const visible = prevScrollPosition > currentScrollPos;

    this.setState({
      prevScrollPosition: currentScrollPos,
      visible
    });
  };

  componentDidMount() {
    window.addEventListener("scroll", this.handleScroll);
  }

  componentWillUnmount() {
    window.removeEventListener("scroll", this.handleScroll);
  }

}
