import React, {Component} from 'react';
import './showcase.scss';
import Fade from 'react-reveal/Fade';


import classNames from 'classnames'

import TechnologiesWrapper from "../../components/TechnologiesWrapper";
import ChallengesCarousel from "../ChallengesCarousel";

export default class Showcase extends Component {

  renderContent(){
    const {title, content, challenges, img, stack: {db, lng, tools = []} = {}} = this.props;

    return (
      <>
        <div className={"showcases-details"}>
          <img className={"case-image"} src={img} alt={title}/>
          <div className={"flex-column-no-wrap-stretch project-details"}>
            <div className="text case-description">
              {content}
            </div>
            <ChallengesCarousel
              className={"carousel"}
              challenges={challenges}
            />
          </div>
        </div>
        <div className="technologies">
          <TechnologiesWrapper header={"Languages"} items={lng}/>
          <TechnologiesWrapper header={"Databases"} items={db}/>
          <TechnologiesWrapper header={"Tools"} items={tools}/>
        </div>
      </>
    )
  }

  render() {
    const {id, reverse, title, index} = this.props;

    return (
      <div className={classNames("showcase", "print-page-break-after", reverse && "reverse" )}>
        <h2 id={id} className="header">
          {title}
        </h2>
        {
          index > 0 ? (
            <Fade big>
              {this.renderContent()}
            </Fade>
          ) : (
            this.renderContent()
          )
        }
      </div>
    )
  }
}
