import React, {Component} from 'react';
import {OverlayTrigger, Tooltip} from 'react-bootstrap'

import "./images-wrapper.scss"


export default class ImagesWrapper extends Component {

    render() {
        const {items} = this.props;
        if(items == null) {
            return null;
        }

        return (
            <div className={"slider"}>
                {items.map(tool =>
                    <div key={tool.alt} className={"tool-image-wrapper"}>
                        <OverlayTrigger
                            placement='bottom'
                            overlay={
                                <Tooltip id={tool.alt}>{tool.alt}</Tooltip>
                            }>
                            <img className={"tool-image"} src={tool.img} alt={tool.alt}/>
                        </OverlayTrigger>
                      <span className="tool-alt for-print">{tool.alt}</span>
                    </div>
                )}
            </div>

        );
    }
}
