import _ from 'lodash';

const getDimensions = () => {
  const {screen} = window;
  const {height, orientation = 0} = screen;
  const {width} = document.body.getBoundingClientRect();
  return {width, height, orientation};
};

const generateHashFromTitle = (title) => {
  return _.snakeCase(title).replace(/_/, '-')
}

export {
  getDimensions,
  generateHashFromTitle
}
