import React, {Component} from 'react';
import './hex.scss';
import {COLORS} from '../../constants';

export default class Index extends Component {
  render() {
    const {img, textColor, color, text, faded = true } = this.props;

    const primaryColor = color || COLORS.PRIMARY;

    return (
      <div className="hex-parent-container">
        <svg viewBox="0 0 180 200" xmlns="http://www.w3.org/2000/svg">
          <defs>
            <solidColor id="asd" solid-color="red"/>
            <clipPath id="cut-off-image">
              <polygon points="20,60 90,20 160,60 160,140 90,180 20,140"/>
            </clipPath>
            <clipPath id="cut-off-top">
              <polygon points="20,60 90,20 160,60 160,140 90,180 20,140"/>
            </clipPath>
          </defs>
          {
            img ? (
              <image href={img} x={20} y="20" height="160" width="140"
                     preserveAspectRatio="xMinYMin slice"
                     clipPath="url(#cut-off-image)"/>
            ) : null
          }
          {
            color ? (
              <polygon points="20,60 90,20 160,60 160,140 90,180 20,140" fill={color}/>
            ) : null
          }
          {
            faded  ? (
              <polygon points="20,60 90,20 160,60 160,140 90,180 20,140" fill="rgba(0,0,0,0.7)"/>
            ) : null
          }
          <polygon points="20,60 90,20 160,60 160,140 90,180 20,140"
                   strokeWidth="1" stroke={primaryColor} fill="transparent"/>
          <polygon id="scalable-stroke" points="8,52 90,5 172,52 172,148 90,195 8,148"
                   strokeWidth="1" stroke={primaryColor} fill="transparent"/>
        </svg>
        <div className="text-container">
          <p style={{color: textColor}}>
            {text}
          </p>
          {this.props.children}
        </div>
      </div>
    )
  }
}

