import {Component} from "react";
import React from "react";
import './showcases.scss'
import Showcase from "../../components/Showcase";
import source from './source.js';
import {generateHashFromTitle} from '../../utils';

export default class Showcases extends Component {
  constructor(props){
    super(props);
  }

  renderCase(item, i){
    return (
      <Showcase id={generateHashFromTitle(item.title)} key={item.title} {...item} index={i} reverse={i % 2 === 0}/>
    )
  }

  componentDidMount(){
    const suite = window.location.hash.replace("#", "");

    if(suite){
      try {
        const element = document.getElementById(suite);
        element && element.scrollIntoView()
      } catch (e) {
        console.warn("Case not found:", suite)
      }
    }
  }

  render() {
    return (
      <div className="showcases">
        <div ref="container" className="showcases-content">
          <h1 className="for-print">koitechs</h1>
          <h1>Case Studies</h1>
          {source.showcases.map(this.renderCase)}
          <span className="print not-for-print">
          <img src="../print.png" alt="Print" onClick={()=> window.print()}/>
        </span>
        </div>
      </div>
    )
  }
}

