import React, { Component } from 'react';
import {Polygons, Navbar} from "./components"
import {Landing, Showcases, Hiring} from './scenes';
import { BrowserRouter as Router, Route } from "react-router-dom";
import Footer from "./components/Footer";
import {getDimensions} from './utils'

import './style/print.scss'
import './style/commons.scss'


class App extends Component {
  constructor(props) {
    super(props);

    this.state = {
      scrolledValue: 0,
      ...getDimensions()
    }
  }

  componentDidMount(){
    getDimensions();
    window.onresize = () => this.setState(getDimensions());
    window.onorientationchange = () => this.setState(getDimensions())
  }

  componentWillUnmount(){
    window.onresize = () => {};
    window.onorientationchange = () => {};
  }

  render() {
    const {width, height} = this.state;
    const dimensionsKey = ''+width+height;

    let onScroll = event => {
       // noop TODO make better solution for parallax
    };

    return (
      <div className="App">
        <Polygons key={dimensionsKey} width={width} height={height * 1.5}
                  scrolledValue={this.state.scrolledValue}/>
        <Router>
          <Navbar/>
          <Route path="/" exact render={(props) => <Landing {...props} onScroll={onScroll}/>}/>
          <Route path="/case-studies/" component={Showcases} />
          <Route path="/hiring/" component={Hiring} />
          <Footer/>
        </Router>
      </div>
    );
  }
}

export default App;
