import React, {Component} from 'react';
import './contacts-section.scss'
import {InputGroup, FormControl} from 'react-bootstrap';
import axios from 'axios';
import * as validator from 'email-validator';
import PhoneInput, {isValidPhoneNumber} from 'react-phone-number-input'
import 'react-phone-number-input/style.css'

const defaultState = {
  name: "",
  phoneNumber: "",
  email: "",
  content: "",
  validationError: false,
  success: null,
  error: null
};


export default class ContactsSection extends Component {
  constructor(props) {
    super(props);

    this.state =  defaultState
  }

  handleChangeEvent(key, event) {
    let value = event.target.value;
    let valid = this.isFormValid();
    this.setState({[key]: value, validationError: !valid})
  }

  handleChangeValue(key, value) {
    let valid = this.isFormValid();
    this.setState({[key]: value, validationError: !valid})
  }

  sendRequest({ name, phoneNumber, email, content}) {
    this.setState({disabled : true}, () => {
      axios.post('https://bs31uzsq97.execute-api.us-east-1.amazonaws.com/dev/koitechs/send-email',
        { name, phoneNumber, email, content},
        { headers: {'Content-Type': 'application/json'}
        })
        .then((response) => {
          this.setState({...defaultState, success: "Submitted successfully!"}, () => {
            setTimeout(() => this.setState({success: null, disabled: false}), 5000)
          })
        })
        .catch((error) => {
          this.setState({error: true}, () => {
            alert("It seems that something went wrong sending your message, try using contact email or other tools listed next to the form.")
            setTimeout(() => this.setState({error: null, disabled: false}), 5000)
          })
        })
    });
  }

  isFormValid(fromSubmit){
    const {phoneNumber, email, content} = this.state;

    let emailError, phoneError, valid;

    valid = true;

    if(!validator.validate(email)){
      emailError = "Please enter valid email"
    }

    if(!isValidPhoneNumber(phoneNumber)) {
      phoneError = 'Phone number is invalid'
    }

    if(fromSubmit) {
      !content && (valid = false);
      emailError && phoneError && (valid = false);
    } else {
      if(content) {
        emailError && phoneError && (valid = false);
      }
    }

    return valid;
  }

  onSubmit(){
    const {disabled} = this.state;

    if(!this.isFormValid(true)){
      return null;
    }

    if(disabled) {
      return null;
    }

    this.sendRequest(this.state);
  }

  render() {
    let submitMessage = this.state.success || this.state.error || "Submit!";

    return (
      <div className="main-block">
        <span className="large-page-number four">
          04
        </span>
        <div className="block contacts-form fade-in">
          <InputGroup>
            <FormControl value={this.state.name} onChange={this.handleChangeEvent.bind(this, 'name')} placeholder={'Name'}/>
          </InputGroup>
          <InputGroup>
            <FormControl value={this.state.email} onChange={this.handleChangeEvent.bind(this, 'email')} placeholder={'Email'}/>
          </InputGroup>
          <div className="phone-container">
            <PhoneInput international={true} showCountrySelect={false} value={this.state.phone} onChange={this.handleChangeValue.bind(this, 'phoneNumber')} placeholder={'Phone number'}/>
          </div>
          <InputGroup>
            <FormControl  as="textarea" value={this.state.text} rows={6}
                          onChange={this.handleChangeEvent.bind(this, 'content')}
                          placeholder={'What\'s on your mind?'}/>
          </InputGroup>
          { this.state.validationError ? (
            <span className="error-message">Please enter either valid email or phone number and message text</span>
          ) : null}
          <span className="submit" onClick={this.onSubmit.bind(this)}>
              {submitMessage}
            </span>
        </div>
        <div className="block" data-before="Do you need technical consultant?" data-after="Responding to every letter">
          <h2 style={{fontSize: 76, marginBottom: 100}}>Let's have a chat?</h2>
          <p style={{width: "100%", textAlign : "center", marginBottom: 100}}>
            We are pleased to answer any questions <br/>
            and discuss business with You
          </p>
        </div>
      </div>
    )
  }
}
