import React, {Component} from 'react';
import {Carousel, CarouselItem} from "react-bootstrap"

import "./challenges-carousel.scss"
import {Swipeable} from 'react-swipeable';


export default class ChallengesCarousel extends Component {


    constructor(props) {
        super(props);

        this.state = {
            activeIndex: 0,
            direction: null
        }
    }

    onSwipedLeft(event) {
        const totalCount = this.props.challenges.length;
        const nextIndex = this.state.activeIndex - 1;

        this.setState({
            activeIndex: nextIndex < 0 ? totalCount : nextIndex,
            direction: undefined
        })
    }
    onSwipedRight(event) {
        const totalCount = this.props.challenges.length;
        const nextIndex = this.state.activeIndex + 1;

        this.setState({
            activeIndex: nextIndex > totalCount ? 0 : nextIndex
        })
    }

    handleSelect(selectedIndex, e) {
        this.setState({
            activeIndex: selectedIndex,
            direction: e.direction
        });
    }

    render() {
        const { activeIndex } = this.state;
        const {challenges} = this.props;

        return (
            <Swipeable trackMouse
                       onSwipedLeft={this.onSwipedLeft.bind(this)}
                       onSwipedRight={this.onSwipedRight.bind(this)}
                       style={{height: '100%'}}>
                <Carousel
                    activeIndex={activeIndex}
                    fade
                    interval={5000}
                    pauseOnHover
                    onSelect={this.handleSelect.bind(this)}
                    className={"challenges-carousel"}>
                    {
                        challenges.map(({challenge, solution, testimonials}, index) => (
                            <CarouselItem  className={"carousel-item"} key={index}>
                                <h6>Challenge: </h6>
                                <p dangerouslySetInnerHTML={{__html: challenge}}/>
                                <h6>Solution: </h6>
                                <p dangerouslySetInnerHTML={{__html: solution}}/>
                            </CarouselItem>
                        ))
                    }
                </Carousel>
            </Swipeable>

        );
    }
}
