import React, {Component} from 'react';
import './about-section.scss'
import Hex from "../../../components/Hex";
import {COLORS} from "../../../constants";

export default class Capabilities extends Component {
  constructor(props) {
    super(props);

    this.state = {
      active: 0
    }
  }

  render() {
    const points = [
      "full-stack web development",
      "architecture consultancy",
      "UI/UX design",
      "BI solutions construction",
      "mobile development",
    ];

    const changeActive = (active) => this.setState({active});
    return (
      <div className="main-block">
        <div className="block flexible attaches-center" data-before="we are capable of" data-after="but not limited to.">
          <div className="points-list">
            <ul>
              {
                points.map((point, i) => (
                  <li className={i === this.state.active ? 'active' : null}
                      key={point}
                      onMouseOver={changeActive.bind(this, i)}>
                    <span className="digits">0{i}</span>
                    {point}
                  </li>
                ))
              }
            </ul>
          </div>
        </div>
        <div className="block hide-medium">
          <div className="block center flex fade-in fade-right">
            <div className="capabilities">
              <div className="capability">
                <Hex img="./team_resized.jpg" imgOffsetX={-30}>
                  <p style={{fontSize: "2em", color: COLORS.PRIMARY}}>
                    Web/Mobile
                  </p>
                </Hex>
              </div>
              <div className="capability">
                <Hex img="./team_resized.jpg" imgOffsetX={-30}>
                  <p style={{fontSize: "2em", color: COLORS.PRIMARY}}>
                    BI/DevOps
                  </p>
                </Hex>
              </div>
              <div className="capability">
                <Hex img="./team_resized.jpg" imgOffsetX={-30}>
                  <p style={{fontSize: "2em", color: COLORS.PRIMARY}}>
                    Back-end
                  </p>
                </Hex>
              </div>
            </div>
          </div>
        </div>
      </div>
    )
  }
}
