import React, {Component} from 'react';
import {Hex} from '../../../components'
import source from '../../../scenes/Showcases/source'

import './showcases-section.scss';
import {COLORS} from "../../../constants";
import {NavLink} from 'react-router-dom';
import {generateHashFromTitle} from "../../../utils";



const cases = source.showcases.map(({title, img}) => ({title, img}));

export default class ShowcasesSection extends Component {
  renderCase({title, ...other}, index){
    return (
      <NavLink to={`/case-studies#${generateHashFromTitle(title)}`} key={`link-${index}`}>
        <Hex key={`case-${index}`} {...other} text={title}/>
      </NavLink>
    )
  }

  render() {
    return (
      <div className="main-block">
        <span className="large-page-number" style={{right: 10, bottom: 250}}>
          03
        </span>
          <div className={"cases-container"}>
            {cases.map(this.renderCase)}
            <NavLink to={"/case-studies"} key={`link-more`}>
              <div key="more">
                <Hex key={`case-${cases.length}`} color={COLORS.PRIMARY} textColor={"black"} faded={false}>
                  <p style={{fontSize: "1.6em", color: "black"}}>
                    Discover More
                  </p>
                  <img style={{ height: 70 }} src={'./pixel-arrow-right.png'} alt={'arrow'}/>
                </Hex>
              </div>
            </NavLink>
          </div>
      </div>
    )
  }
}
